* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

:root {
  --primary: gray;
  --secondary: white;
  --primary-variant: lightgray;
  --secondary-variant: gray;
  --primary-text: rgb(251, 251, 83);
  --secondary-text: violet;
  --disabled: darkgray;
  --disabled-text: rgb(37, 37, 37);
  --error: rgb(255, 83, 83);
  --helper: rgb(83, 255, 83);
  --info: rgb(83, 83, 255);
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.flex {
  display: flex;
}

.row-reverse {
  flex-direction: row-reverse;
}

.column {
  flex-direction: column;
}

.column-reverse {
  flex-direction: column-reverse;
}

.center {
  justify-content: center;
}

.align-center {
  align-items: center;
}

.space-between {
  justify-content: space-between;
}

.space-evenly {
  justify-content: space-evenly;
}

.space-around {
  justify-content: space-around;
}

.start {
  justify-content: flex-start;
}

.end {
  justify-content: flex-end;
}

.wrap {
  flex-wrap: wrap;
}

.no-wrap {
  flex-wrap: nowrap;
}

.margin-center {
  margin: 0 auto;
}

.w-100 {
  width: 100%;
}

.w-80 {
  width: 80%;
}

.w-75 {
  width: 75%;
}

.w-50 {
  width: 50%;
}

.w-33 {
  width: 33.33%
}

.w-25 {
  width: 25%;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.fixed {
  position: fixed;
}

a {
  color: inherit;
  text-decoration: none;
}

/* Medium devices (tablets, 768px and up) */

@media (min-width: 768px) {
  .md-row {
    flex-direction: row;
  }
  .md-column {
    flex-direction: column;
  }
  .w-md-100 {
    width: 100%
  }
  .w-md-80 {
    width: 80%;
  }
  .w-md-75 {
    width: 75%;
  }
  .w-md-50 {
    width: 50%;
  }
  .w-md-33 {
    width: 33.33%
  }
  .w-md-25 {
    width: 25%;
  }
}

